.cover_wrap {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8) url(./img/cover.b71d0c68.png) no-repeat top right;
}
@keyframes ani_c {
0% {
    transform: translate3d(0, 0, 0);
}
50% {
    transform: translate3d(0, -20px, 0);
    /*no*/
}
100% {
    transform: translate3d(0, 20px, 0);
    /*no*/
}
}
@keyframes ani_down {
0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
50% {
    transform: translate3d(0, 10px, 0);
    /*no*/
    opacity: 1;
}
100% {
    transform: translate3d(0, 20px, 0);
    /*no*/
    opacity: 1;
}
}

.tip_cover {
  position: fixed;
  top: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  -webkit-tap-highlight-color: transparent;
}
.tip_cover .tip_w {
    position: absolute;
    top: 48%;
    left: 0;
    transition: opacity 0.5s;
    -webkit-transition: opacity 0.5s;
    width: 100%;
    text-align: center;
}
.tip_cover .tip_w .cont {
      display: inline-block;
      background: rgba(0, 0, 0, 0.7);
      max-width: 50%;
      padding: 20px 40px;
      /*no*/
      border-radius: 10px;
      /*no*/
      font-size: 28px;
      /*no*/
      color: #fff;
      text-align: center;
}
.tip_cover .tip_w.hide {
      opacity: 0;
}
